.ImageCarousel_Container {
    width: 100%;
    height: fit-content;
    margin: 0;
    box-sizing: border-box;
    padding-top: 1rem;
    margin-top: 10px;
    
    flex-grow: 1;
}

.ImageCarousel_Wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    justify-content: center;
}

.ImageCarousel_TitleContainer {
    width: 100%;
    height: fit-content;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
}

.Pagination_Container {
    width: 100%;
    height: fit-content;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Pagination_Menu {
    display: flex;
    justify-content: center;
}

.Cards_Container {
    width: 100%;
    /*max-width: 400px;*/
    max-width: 55%;
    /*height: fit-content;*/
    margin: 0;
    padding-top: 8px;
    padding-right: 3rem;
    padding-left: 3rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 100%;
    max-height: 400px;
}

.Carousel_Button {
    width: 43px;
    height: 43px;
}

@media only screen and (min-width: 1241px) {
    .ImageCarousel_Container {
        display: none;
    }
}
